body {
  --light-blue: #00acdc;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1100px;
  margin: 50px auto auto;
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-ul {
  justify-content: center;
  align-items: center;
  list-style-type: none;
  display: flex;
}

.header-nav-ul-li {
  margin-right: 20px;
}

.header-nav-ul-li:nth-child(8) {
  margin: 0;
}

.page {
  text-transform: uppercase;
  color: #000;
  flex-direction: column;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.page:hover {
  color: var(--light-blue);
}

.is-active:after {
  content: "";
  background-color: var(--light-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

